
import {defineComponent} from "vue";

export default defineComponent({
    name: "BaseSelect",
    props: {
        label: {
            type: String,
            default: null,
        },
        options: {
            type: Array,
            default: () => [],
        },
        // preselectedValue: {
        //   type: String,
        //   default: null,
        // },
        errors: {
            type: Array,
            default: () => [],
        },
        isValidData: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            selectedValue: "",
        };
    },
    methods: {
        changeHandler(e: Event) {
            const target = e.target as HTMLInputElement;
            this.$emit("update:modelValue", target.value);
        },
    },
});
