
import {defineComponent} from "vue";

export default defineComponent({
    name: "BaseInput",
    props: {
        label: {
            type: String,
            default: null,
        },
        errors: {
            type: Array,
            default: () => [],
        },
        isValidData: {
            type: Boolean,
            default: false,
        },
    },
    methods: {
        inputHandler(e: Event) {
            const target = e.target as HTMLInputElement;
            this.$emit("update:modelValue", target.value);
        },
    },
});
